                "TCGAhnsc": "Head + Neck (TCGA)",
                "TCGAlgg" : "Lower Grade Glioma (TCGA)",
                "TCGAluad": "Lung Adenocarcinoma (TCGA)",
                "TCGAlung": "Lung (TCGA)",
                "TCGAlusc": "Lung Sasquamous Cell (TCGA)",
                "TCGApaad": "Pancreas (TCGA)",
                "TCGAprad": "Prostate (TCGA)",
