$templateCache.put("app/components/landing/landing.html","<div class=\"landing container\"><div class=\"row\" style=\"padding-top:20px;\"><h2>Bridging Cancer Research and Clinical Care</h2></div><div class=\"row\" style=\"padding-top:20px;\"><div class=\"col-sm-8\"><p>Oncoscape is developed at the Fred Hutchinson Cancer Research Center under the auspices of the Solid Tumor Translational Research initiative. Oncoscape is a web-based, menu-driven analysis and visualization platform for large-scale, heterogeneous clinical and molecular patient timeline data as exemplified by the Fred Hutch HIDRA database. Oncoscape vision and development is managed by a small team comprising: <a href=\"https://www.fredhutch.org/en/labs/profiles/holland-eric.html\">Eric Holland</a>, <a href=\"http://www.sttrcancer.org/en/contact-us/horse-grant-bio.html\">Desert Horse-Grant</a>, <a href=\"https://www.linkedin.com/in/lisa-mcferrin-9758b029\">Lisa McFerrin</a>, <a href=\"https://www.linkedin.com/in/hbolouri\">Hamid Bolouri</a>, <a href=\"https://www.linkedin.com/in/jenny-zhang-ba62a2104\">Jenny Zhang</a>, <a href=\"https://www.linkedin.com/in/gretchenh\">Gretchen Heinrich</a>, <a href=\"https://www.linkedin.com/in/robert-mcdermott-a77b9011\">Robert McDermott</a> and <a href=\"https://www.linkedin.com/in/zager\">Michael Zager</a>.</p><p class=\"\">Through its easy point-and-click browser interface, Oncoscape will become an essential tool for doctors and researchers interested in:</p><ul class=\"\"><li>Comparing patients or populations across an array of features, like genetic alterations (CNV, IHC, sequencing, expression) or clinical elements (treatment regimens, diagnostics, outcomes)</li><li>Defining cohorts based on selected traits  identifying patients like me</li><li>Aligning patient histories according to timeline events: diagnosis, treatment, progression, survival</li><li>Performing powerful and integrated statistical analysis: Principle Component Analysis, Partial least squares regression, Kaplan-Meier curves, enrichment scores</li><li>Creating high quality visualizations of molecular/clinical data</li></ul><p class=\"\">By providing a portal equipped with rich data, powerful statistics and a simple interface, Oncoscape not only empowers users to quickly answer questions themselves but also expedites the process of discovering novel patterns and relationships. This can lead to numerous possible findings, including optimized drug therapies for individual patients, identification of novel regulatory pathways, or even the need to reassess adherence strategies.</p></div><div class=\"col-sm-4\"><os-login></os-login></div></div></div>");
